"use client";

import { useMemo, useState } from "react";
import type { Grupo } from "../../lib/api";
import { clientApi } from "../../lib/client";

type Props = {
  grupos: Grupo[];
  filtros: Record<string, string[]>;
};

export default function GroupTable({ grupos, filtros }: Props) {
  const [query, setQuery] = useState("");
  const [selected, setSelected] = useState<Grupo | null>(null);
  const [status, setStatus] = useState<string | null>(null);

  const filtered = useMemo(() => {
    if (!query) return grupos;
    const lower = query.toLowerCase();
    return grupos.filter(
      (grupo) =>
        grupo.nome_grupo.toLowerCase().includes(lower) ||
        (grupo.group_id ?? "").toLowerCase().includes(lower) ||
        (grupo.cr ?? "").toLowerCase().includes(lower)
    );
  }, [grupos, query]);

  async function handleUpdate(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    if (!selected) return;

    const formData = new FormData(e.currentTarget);
    try {
      await clientApi.put(`/grupos/${selected.id}`, {
        group_id: formData.get("group_id"),
        nome: formData.get("nome_grupo"),
        enviar_mensagem: formData.get("envio") === "on",
        cr: formData.get("cr")
      });
      setStatus("Grupo atualizado! Recarregue a página para ver os dados mais recentes.");
    } catch (error: any) {
      setStatus(error.message ?? "Erro ao atualizar");
    }
  }

  return (
    <div style={{ display: "grid", gap: "1.5rem" }}>
      <input
        placeholder="Pesquisar por nome, group_id ou CR"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
      />

      <div style={{ overflowX: "auto" }}>
        <table>
          <thead>
            <tr>
              <th>Nome</th>
              <th>Group ID</th>
              <th>CR</th>
              <th>Envio</th>
              <th>Cliente</th>
              <th>Supervisor</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {filtered.map((grupo) => (
              <tr key={grupo.id}>
                <td>{grupo.nome_grupo}</td>
                <td>{grupo.group_id}</td>
                <td>{grupo.cr ?? "—"}</td>
                <td>{grupo.envio ? "Sim" : "Não"}</td>
                <td>{grupo.cliente ?? "—"}</td>
                <td>{grupo.supervisor ?? "—"}</td>
                <td>
                  <button type="button" onClick={() => setSelected(grupo)}>
                    Editar
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {selected && (
        <form className="panel" onSubmit={handleUpdate}>
          <h3>Editar grupo: {selected.nome_grupo}</h3>
          <div style={{ display: "grid", gap: "0.75rem" }}>
            <label>
              Group ID
              <input name="group_id" defaultValue={selected.group_id} required />
            </label>
            <label>
              Nome
              <input name="nome_grupo" defaultValue={selected.nome_grupo} required />
            </label>
            <label>
              CR
              <input name="cr" defaultValue={selected.cr ?? ""} />
            </label>
            <label style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
              <input type="checkbox" name="envio" defaultChecked={selected.envio} />
              Habilitar envio
            </label>
            <button type="submit">Salvar alterações</button>
            {status && <p style={{ margin: 0 }}>{status}</p>}
          </div>
        </form>
      )}
    </div>
  );
}
